  padding: 15px 15px 0 15px;
}
.list2 .list_main {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-flow: column;
            flex-flow: column;
}
.list2 .list_main .list_row {
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -webkit-flex-flow: column;
              flex-flow: column;
}
.list2 .list_main .list_row .title {
        -webkit-box-flex: 1;
        -webkit-flex: 1;
                flex: 1;
        color: #000;
        font-weight: 600;
}
