  color: #ffd748 !important;
}
html, body {
  width: 100%;
  height: 100%;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  color: #999;
  display: inline-block;
}
.writerArticle {
  width: 100%;
  height: 100%;
