  -webkit-flex: 1;
          flex: 1;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-flow: column;
          flex-flow: column;
  overflow: hidden;
  color: #666;
  font-size: 16px;
  background: #fff;
}
.writerArticle .vux-header {
    width: 100%;
    height: 46px;
    background: #fff;
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 46px;
            flex: 0 0 46px;
