
.router-fade-enter-active, .router-fade-leave-active {
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
.router-fade-enter, .router-fade-leave-active {
  opacity: 0;
}
.router-link-active {
  color: #ffd748 !important;
}
html, body {
  width: 100%;
  height: 100%;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  color: #999;
  display: inline-block;
}
.container {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  z-index: 99;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  background: black;
}
.container .cropper-crop-box,
  .container .cropper-modal {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}
.container #button {
    position: absolute;
    right: 10px;
    top: 10px;
    width: 80px;
    height: 40px;
    border: none;
    border-radius: 5px;
    background: white;
}
.user_message {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  overflow: hidden;
  color: #666;
}
.user_message .icon {
    margin-right: 15px;
    font-size: 20px;
}
.user_message .icon-fanhui- {
    position: absolute;
    padding: 10px;
    top: 0;
    left: 0;
    color: #fff;
    font-size: 23px;
    margin-right: 0;
}
.user_message .center {
    text-align: center;
    padding-top: 20px;
    color: #fff;
    font-size: 18px;
}
.user_message .center .header_btton {
      margin: 0 auto;
      width: 100px;
      height: 100px;
      position: relative;
      overflow: hidden;
}
.user_message .center .header_btton .picture {
        width: 100px;
        height: 100px;
        margin: 0 auto;
        box-sizing: border-box;
        border-radius: 50%;
        border: 4px solid #ececec;
        overflow: hidden;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
}
.user_message .center .header_btton #change {
        width: 100px;
        height: 100px;
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0;
}
.user_message .center .desc {
      font-size: 15px;
      margin-top: 5px;
      height: 17px;
}
.user_message .center .desc .iconfont {
        margin-left: 10px;
}
.user_message .center .desc .iconfont.icon-xingbie-nan {
          color: #1296db;
}
.user_message .center .desc .iconfont.icon-xingbie-nv {
          color: #d4237a;
}
.user_message .center .bjxx {
      margin: 8px auto 0;
      width: 80px;
      padding: 3px;
      border: 1px solid #c7c7c7;
      border-radius: 15px;
      font-size: 12px;
      color: #fff;
}
.user_message input {
    color: #999;
}
.user_message .user-sex-item {
    border: 1px solid #ececec;
    padding: 5px 15px;
}
.user_message .user-sex-item .sex {
      display: inline-block;
      margin-left: 20%;
}
.user_message .user-sex-item-selected {
    border: 1px solid #ffd748;
}



