
.router-fade-enter-active, .router-fade-leave-active {
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
.router-fade-enter, .router-fade-leave-active {
  opacity: 0;
}
.router-link-active {
  color: #ffd748 !important;
}
html, body {
  width: 100%;
  height: 100%;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  color: #999;
  display: inline-block;
}
.updateMessage {
  width: 100%;
  height: 100%;
  z-index: 1000;
  position: absolute;
  left: 0;
  top: 0;
  background: #fff;
  overflow: hidden;
}
.updateMessage input {
    color: #999;
    font-size: 16px;
}
.updateMessage .sex {
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-box-align: end;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    -webkit-box-flex: 1;
    -webkit-flex: 1;
            flex: 1;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    justify-items: center;
    padding: 10px 0;
}
.updateMessage .sex .demo5 {
      width: 100px;
      height: 26px;
      line-height: 26px;
      text-align: center;
      border-radius: 6px;
      border: 1px solid #ffd748;
      background-color: #fff;
      display: inline-block;
}
.updateMessage .sex .demo5-selected {
      background: #ffffff url(../../../static/img/choice.png) no-repeat right bottom;
      background-size: contain;
}
.updateMessage .submit {
    box-sizing: border-box;
    display: inline-block;
    margin-left: 6.4%;
    width: 40%;
    margin-top: 40px;
    margin-bottom: 20px;
}
.updateMessage .submit.weui-btn_default:not(.weui-btn_disabled):active {
      color: #535353;
      background-color: #eaeaea;
}



