
.router-fade-enter-active, .router-fade-leave-active {
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
.router-fade-enter, .router-fade-leave-active {
  opacity: 0;
}
.router-link-active {
  color: #ffd748 !important;
}
html, body {
  width: 100%;
  height: 100%;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  color: #999;
  display: inline-block;
}
.register .register_header {
  width: 100%;
  height: 40px;
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  font-size: 18px;
  line-height: 40px;
  position: relative;
  background: #ffd748;
}
.register .register_header .header_c {
    position: absolute;
    height: 30px;
    right: 10px;
    top: 0;
}
.register .register_header .header_c a {
      color: #fff;
      font-size: 14px;
      line-height: 40px;
      margin-left: 10px;
      text-decoration: none;
}



