
.router-fade-enter-active, .router-fade-leave-active {
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
.router-fade-enter, .router-fade-leave-active {
  opacity: 0;
}
.router-link-active {
  color: #ffd748 !important;
}
html, body {
  width: 100%;
  height: 100%;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  color: #999;
  display: inline-block;
}
.register {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-flow: column;
          flex-flow: column;
  width: 100%;
  height: 100%;
  position: relative;
}
.register .active_main {
    padding: 10px;
    color: #494949;
    font-size: 14px;
    line-height: 1.8;
}
.register .active_main .nickname {
      text-indent: 1em;
      color: #1989fa;
      font-size: 16px;
      line-height: 2;
}
.register .active_main .submit {
      display: block;
      margin: 20px auto;
      width: 80%;
      color: #fff;
      background: #ffd748;
      outline: none;
      border: 0;
      padding: 8px;
      border-radius: 5px;
      font-size: 18px;
}
.register .active_main .submit:active {
      background: #ffd23c;
}



