
.router-fade-enter-active, .router-fade-leave-active {
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
.router-fade-enter, .router-fade-leave-active {
  opacity: 0;
}
.router-link-active {
  color: #ffd748 !important;
}
html, body {
  width: 100%;
  height: 100%;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  color: #999;
  display: inline-block;
}
.list1 {
  width: 100%;
  box-sizing: border-box;
  padding: 15px 15px 0 15px;
}
.list1 .list_main {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-flow: column;
            flex-flow: column;
}
.list1 .list_main .list_row {
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
      -webkit-flex-flow: row;
              flex-flow: row;
}
.list1 .list_main .list_row .title {
        -webkit-box-flex: 1;
        -webkit-flex: 1;
                flex: 1;
        color: #000;
        font-weight: 600;
}
.list1 .list_main .list_row .img {
        width: 32%;
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 32%;
                flex: 0 0 32%;
}
.list1 .list_main .list_row .img img {
          width: 100%;
          display: inline-block;
}
.list1 .list_main .time {
      color: #666;
      font-size: 12px;
      line-height: 1.5;
      padding-bottom: 15px;
}
.list1 .list_main .time span {
        margin-right: 15px;
}



