
.router-fade-enter-active, .router-fade-leave-active {
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
.router-fade-enter, .router-fade-leave-active {
  opacity: 0;
}
.router-link-active {
  color: #ffd748 !important;
}
html, body {
  width: 100%;
  height: 100%;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  color: #999;
  display: inline-block;
}
.cm_header {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-flow: column;
          flex-flow: column;
  background: #fff;
}
.cm_header .header {
    width: 100%;
    height: 77px;
    background: #fff;
    position: relative;
}
.cm_header .header .top {
      width: 100%;
      height: 44px;
      position: relative;
      background: #ffd748;
      background-image: url(//mat1.gtimg.com/www/mobi/2017/image/siteheader-skin-default.svg);
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 120%;
}
.cm_header .header .top .title {
        color: #fff;
        font-size: 18px;
        line-height: 44px;
        text-align: center;
        letter-spacing: 5px;
}
.cm_header .header .top .user {
        width: 30px;
        height: 30px;
        position: absolute;
        right: 13px;
        top: 50%;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
}
.cm_header .header .top .user .icon-yonghu, .cm_header .header .top .user img {
          width: 30px;
          height: 30px;
          color: #fff;
          font-size: 25px;
          line-height: 30px;
}
.cm_header .header .nav {
      width: 100%;
      height: 33px;
      color: #2f2f2f;
      font-size: 12px;
      line-height: 33px;
      overflow: hidden;
      position: relative;
}
.cm_header .header .nav .item {
        width: 16.6%;
        text-align: center;
        float: left;
        color: #666;
        font-size: 16px;
        line-height: 33px;
        -webkit-transition: all 0.3s ease;
        transition: all 0.3s ease;
}
.cm_header .header .nav .item.more {
          -webkit-transform: rotate(180deg);
                  transform: rotate(180deg);
}
.cm_header .more_tab {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 77px;
    box-sizing: border-box;
    padding-top: 15px;
    background: #f6f6f6;
    right: -100%;
    z-index: 1000;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}
.cm_header .more_tab.translate {
      -webkit-transform: translateX(-100%);
              transform: translateX(-100%);
}



