
.router-fade-enter-active, .router-fade-leave-active {
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
.router-fade-enter, .router-fade-leave-active {
  opacity: 0;
}
.router-link-active {
  color: #ffd748 !important;
}
html, body {
  width: 100%;
  height: 100%;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  color: #999;
  display: inline-block;
}
.comment_component {
  padding-top: 10px;
  box-sizing: border-box;
}
.comment_component .title {
    padding-top: 10px;
}
.comment_component .title:before {
      width: 4px;
      height: 15px;
      content: " ";
      display: inline-block;
      vertical-align: middle;
      background: #ffd748;
      margin-right: 8px;
}
.comment_component .comment_message {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-flow: row;
            flex-flow: row;
    margin-top: 10px;
}
.comment_component .comment_message .user_header {
      width: 40px;
      height: 40px;
      overflow: hidden;
      -webkit-box-flex: 0;
      -webkit-flex: 0 0 40px;
              flex: 0 0 40px;
}
.comment_component .comment_message .user_header img {
        width: 40px;
        height: 40px;
        border-radius: 50%;
}
.comment_component .comment_message .message {
      -webkit-box-flex: 1;
      -webkit-flex: 1;
              flex: 1;
      padding-left: 10px;
      box-sizing: border-box;
}
.comment_component .comment_message .message .vtextarea {
        display: none;
}
.comment_component .comment_message .message .username {
        padding: 3px 0;
        font-size: 13px;
}
.comment_component .comment_message .message .time {
        color: #888;
        font-size: 12px;
        line-height: 1.5;
}
.comment_component .comment_message .message .content {
        padding-top: 10px;
        font-size: 15px;
}
.comment_component .comment_message .message .action {
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
        -webkit-flex-flow: row;
                flex-flow: row;
        -webkit-box-pack: end;
        -webkit-justify-content: flex-end;
                justify-content: flex-end;
        margin-bottom: 20px;
}
.comment_component .comment_message .message .action .item {
          border-radius: 3px;
          padding: 0 5px;
          height: 20px;
          line-height: 22px;
          font-size: 12px;
          margin-left: 20px;
          color: #93afca;
}
.comment_component .comment_message .message .action .item .iconfont {
            font-size: 12px;
}
.comment_component .comment_message .message .action .item span {
            margin-left: 5px;
}



