
.router-fade-enter-active, .router-fade-leave-active {
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
.router-fade-enter, .router-fade-leave-active {
  opacity: 0;
}
.router-link-active {
  color: #ffd748 !important;
}
html, body {
  width: 100%;
  height: 100%;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  color: #999;
  display: inline-block;
}
.article {
  width: 100%;
  height: 100%;
}
.article .top {
    width: 100%;
    height: 47px;
    line-height: 47px;
    padding: 0 12px;
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
}
.article .top .iconfont {
      display: inline-block;
      color: #666;
      font-size: 24px;
      line-height: 47px;
}
.article .top .iconfont.icon-daohang {
        position: absolute;
        padding-right: 16px;
        right: 0;
}
.article .article_main {
    width: 100%;
    padding: 0 15px 150px 15px;
    box-sizing: border-box;
}
.article .article_main .title {
      padding: 20px 0;
      color: #222;
      font-size: 22px;
      line-height: 1.3;
      letter-spacing: 2px;
}
.article .article_main .author_time {
      color: #545454;
      font-size: 12px;
      line-height: l 0.3;
}
.article .article_main .author_time span {
        margin-right: 10px;
}
.article .article_main .article_content {
      padding: 20px 0 0 0;
      word-wrap: break-word;
      word-break: break-all;
      text-align: justify;
      color: #222;
      font-size: 16px;
}
.article .article_main .article_content img {
        display: inline-block;
        width: 100%;
        height: 200px;
        margin-top: 10px;
}
.article .userlun {
    width: 100%;
    height: 48px;
    position: fixed;
    background: #fff;
    bottom: 0;
    left: 0;
    padding: 12px;
    margin-top: 150px;
    box-sizing: border-box;
}
.article .userlun .tbas {
      width: 50%;
      text-align: center;
      float: left;
      color: #222 !important;
}
.article .userlun .tbas .iconfont {
        margin-right: 10px;
        font-size: 20px;
}
.article .more_tab {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 47px;
    box-sizing: border-box;
    padding-top: 15px;
    background: #f6f6f6;
    right: -100%;
    z-index: 1000;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}
.article .more_tab.translate {
      -webkit-transform: translateX(-100%);
              transform: translateX(-100%);
}
.article .comment {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    box-sizing: border-box;
    background: #fff;
    right: -100%;
    z-index: 1000;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    overflow: hidden;
}
.article .comment.commentActive {
      -webkit-transform: translateX(-100%);
              transform: translateX(-100%);
}
.article .comment .comment_top {
      width: 100%;
      height: 47px;
      line-height: 47px;
      text-align: center;
      box-sizing: border-box;
      position: relative;
      font-size: 20px;
}
.article .comment .comment_top .iconfont {
        font-size: 24px;
        position: absolute;
        left: 0;
}
.article .comment .comment_top span {
        font-size: 18px;
}
.article .comment .textarea {
      width: 90%;
      margin: 10px auto;
}
.article .comment .comment_list {
      clear: both;
      margin-top: 80px;
      padding: 12px 12px 150px 12px;
}



